<template>
<div>
 
  <v-row
    align="center"
    justify="center"
  >
    <v-col cols="12">
      <v-hover
        v-slot:default="{ hover }"
        disabled
      >
        <v-card
          :elevation="hover ? 12 : 6"
          class="mx-auto"
          height="700"
          max-width="1024"
        >
          <v-card-text class="my-4 text-center title">Categorias</v-card-text>

<div  id="formulario" class="font-weight-light grey--text title mb-4 my-6 text-center" >
<v-form
    ref="form"
    v-model="valid"
    lazy-validation
  >
    <v-text-field
      v-model="name"
      :counter="10"
      :rules="nameRules"
      label="Name"
      required
    ></v-text-field>


    <v-select
      v-model="select"
      :items="items"
      label="Categorias Padre"
      required
      @click="onSelectPicked"
    ></v-select>
    
      <v-textarea
          outlined
          counter
          name="input-7-4"
          label="Descripcion"
          v-model="descripcion"
          :rules="rules"
        ></v-textarea>
<p>Seleccione Imagen
  <input
    type="file"
    @change="onFilePicked"
    name="input"
  >
  </p>


 

    <v-checkbox
      v-model="checkbox"
      :rules="[v => !!v || 'You must agree to continue!']"
      label="Do you agree?"
      required
    ></v-checkbox>

    <v-btn
      :disabled="!valid"
      color="success"
      class="mr-4"
      @click="validate"
    >
      Guardar Categoria
    </v-btn>

    <v-btn
      color="info"
      class="mr-4"
      @click="reset"
    >
      Reset FOrmulario
    </v-btn>


  </v-form>

</div>
 <div v-if="validatee" style="margin:50px;">
  <v-alert type="success">
      Categoria Guardada
    </v-alert>
  </div>

   <div v-if="seleccioneimagen" style="margin:50px;">
  <v-alert type="error">
      Seleccione una Imagen. Gracias
    </v-alert>
  </div>

 </v-card>
      </v-hover>
    </v-col>
  </v-row>
</div>
</template>

<script>
import { mapGetters } from 'vuex';

  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 30) || 'Name must be less than 30 characters',
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      items: [],   
      select: "",
      checkbox: false,
      descripcion:'',
      File:[],
      image:"",
      send:true,
      files:null,
      id:'',
      categoriasTodas:[],
      validatee:false,
      rules: [v => v.length <= 25 || 'Max 25 characters', v => v.length != 0 || 'Campo necesario'],
      seleccioneimagen:false,
    }),

    methods: {
      ...mapGetters([
        'getToken',
        'getUsername',
      ]),
      validate () {
        this.$refs.form.validate()
       // console.log("name"+this.name + " " +"checkbox"+ this.checkbox)
       const valor = ((this.name=='' || this.name == undefined) || (!this.checkbox || this.checkbox==undefined) )?false:true;
        console.log("retorna valor"+valor)
        if(valor){this.SendDataForm()}
      
      },
      reset () {
        this.$refs.form.reset()
        this.files = null;
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
       SendDataForm: function(){
         //mandar la info a los actions y despues a django - postgres
        //del select sacar el id de la categoria padre si la selecciona

        if(this.categoriasTodas.length != 0)
        {
          this.categoriasTodas.forEach((element,index)=>{
            if(element.name == this.select){
              this.id = element.id;
          }
          })
        }
        else
        { 
          console.log("Do notihing")
        }
   

      if(this.files != null){
        console.log("id de la categoria padre"+this.id)
        let datos ={
          token:this.getToken(),
          name:this.name,
          descripcion:this.descripcion,
          cateFather:this.id,
          id_Usuario:this.getUsername().id

        }

        //Objeto que lleva a CategoriaPost
        this.$store.dispatch('api_django_saveCategories',datos)
        .then(res=>{
          console.log(res.data.categoria.id)
         
          let datos2 = {
            token:this.getToken(),
            id:res.data.categoria.id,
            photo:this.files
          }

        this.$store.dispatch('api_django_imageCategorie',datos2)
        .then(res=>{
          console.log(res.data)
          //Alert aca de operacion exitosa
          this.reset()
          this.savevalidate(true)
          this.myvalidateFunction()
         
        }).catch(error=>{
          console.log(error)
        })
          //hacer cosas aca con la ID que me retorna el postear una categoria
        }).catch(error=>{
          console.log(error)
        })
       }else{
         this.seleccioneimagen = true;
         this.myvalidateFunction2()

       }
        
       },
    
    
    onFilePicked(event){

      
     this.files = event.target.files[0];
     console.log(this.files)
    },
    onSelectPicked(){
      console.log("hola"+this.select)
    },
           getCategoriasList: function(){
           let datos3 ={
               token:this.getToken(),
                id:this.getUsername().id,
           }
           this.$store.dispatch('api_django_getCategoriasSola',datos3)
           .then(res=>{
             if(res.data.Error != "No existe esa categoria"){
               console.log(res.data)
              this.categoriasTodas = res.data;
              
              res.data.forEach((element,index) => {
              this.items.push(element.name)
               });
             }else{
               console.log("No hay datos")
             }
            
         
           }).catch(error=>{
               console.log(error)
           })
       },
       savevalidate(bool){
         this.validatee = bool;
       },
         myvalidateFunction(){
        setTimeout(() => this.validatee = false, 10000);
              },
                myvalidateFunction2(){
        setTimeout(() => this.seleccioneimagen = false, 5000);
              },
  },computed:{
    
    showSelect: function(){
    }
},
created(){
  this.getCategoriasList()
}
  
    }
  
  
</script>



