<script>
//crear v-if que evalue si lo que se selecciono fue categorias, tema nuevo o tabla de dato
import { mapGetters, mapMutations } from 'vuex';
import Categorias from './Categorias';
import CateForm from './CateForm';
import ListaCategoria from './ListCategories';
import Tema from './Temas/Temas';
import PanelTemaNuevo from './Temas/NuevoTema/FormularioTemaNuevo';
