import PanelListaSubCategorias from './SubCategorias/subcategorias';
import PanelTemaScreen from './Temas/temascreen/temascreen'
import LayoutInicio from '../views/Layout';
import Perfil from './Perfil/perfil'
import Home from '../views/Layout';

  export default {
    components:{
