      CateForm,
      ListaCategoria,
      Tema,
      PanelTemaNuevo,
      TemaLista,
      PanelListaSubCategorias,
      PanelTemaScreen,
      LayoutInicio,
      Perfil,
    },
    data () {
      return {
        drawer: true,
