                  this.setPanelCategorias(true);
                  console.log("cate")
                  return true;
                  break;
            case "Temas":
                  this.setPanelTema(true);
                  console.log("tema")
                  return true;
                  break;
            case "Perfil":
                 this.setPanelPerfil(true);
                  console.log("tabla")
                  return true;
                  break;
            case "Home":
              this.setPanelInicio(true);
              return true;
              break;
            
            default:
          }          
        },
        evluaSeleccion: function(){
          if(this.getPanelCategorias() === "Categorias"){return true}
          else if(this.getPanelCategorias() === "Tema Nuevo")return true
          else if(this.getPanelCategorias() === "Tabla de Datos")return true
        }
       
      
    },
    computed:{
      saludo: function(){
        console.log("hola mundo")
      }
    },
    created(){
    }
  }
